:root {
  --maincolor: #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--maincolor);
}

.is-active {
  color: var(--maincolor);
  position: relative;
}

.is-active:after {
  content: "";
  color: var(--maincolor);
  border-bottom: 4px solid var(--maincolor);
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

